const Swim = styled.div`
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 150px;
  left: -150px;
  animation: ${slide} 0.3s ease-in;
`;
const Loading = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 0 0 15px gray;
  font-size: 20px;
  background-image: url("https://post-phinf.pstatic.net/MjAxOTA4MjNfMTQy/MDAxNTY2NTM5Njk3MDc1.d-pxKdg0Ni8cppT3_g-TfaoL7eBjWdaQD7YsocRRpzIg.ftK-ZuCMq8X1Q-oeENQa5v1QdXPiSdNm7Gr8UuWdOJAg.JPEG/1%ED%99%94_%EB%B0%94%EB%8B%A4.jpg?type=w1200");
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
  font-family: "Black Han Sans", sans-serif;
`;

async function fetchData() {
  const response = await axios.get("/getBeachCongestionApi.do");
  return response.data;
}

function Items({ poiNm, uniqPop, congestion }) {
  return (
    <BeachItem congestion={congestion}>
      <Darker>
        <ItemTitle>{poiNm}</ItemTitle>
        <ItemInfo>
          <li>
            {congestion === "1" && (
              <i>
                <RiEmotionHappyLine
                  style={{ fontSize: "35px", color: "green" }}
                />
              </i>
            )}
            {congestion === "2" && (
              <i>
                <RiEmotionNormalLine
                  style={{ fontSize: "35px", color: "orange" }}
                />
              </i>
            )}
            {congestion === "3" && (
              <i>
                <RiEmotionUnhappyLine
                  style={{ fontSize: "35px", color: "red" }}
                />
              </i>
            )}
          </li>
          <li>
            <small>주변 사람수:</small> {uniqPop} <small>명</small>
          </li>
        </ItemInfo>
      </Darker>
    </BeachItem>
  );
}
export const SearchText = createContext();

export default function List() {
  const [state] = useAsync(fetchData, []);
  const { loading, data: beachs, error } = state;
  const [text, setText] = useState("");
  const searchClose = useContext(searchOpen);
  const searchSetClose = useContext(searchSetOpen);
  if (loading)
    return (
      <Loading>
        <Darker loading>
          LOADING..
          <Swim>
            <FaSwimmer style={{ fontSize: "50px" }} />
          </Swim>
        </Darker>
      </Loading>
    );
  if (error) return <div>에러발생</div>;
  if (!beachs) return <div>데이터 없음</div>;
  const arr = Object.values(beachs);

  return (
    <SearchText.Provider value={setText}>
      <ItemContainer>
        {arr.map(
          (beach) =>
            beach.poiNm.includes(`${text}`) && (
              <Items
                key={beach.seqId}
                uniqPop={beach.uniqPop}
                congestion={beach.congestion}
                etlDt={beach.etlDt}
                poiNm={beach.poiNm}
              />
            )
        )}
        <Nav
          SearchText={SearchText}
          onclick={() => searchSetClose(!searchClose)}
        />
      </ItemContainer>
    </SearchText.Provider>
  );
}
